import React, { useState, useEffect } from 'react';
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import moment from 'moment'
import { Select, Table, Popover, Form, Modal, Button, Row, Col, Input } from 'antd';
import './ElasticIp.css'
import * as elastic from '@/api/elastic_ip'
import * as cloud from '@/api/cloud'
import { getCommon } from '@/api/common'
import { showDialog } from '@/utils/util'
import DelDialog from '@/components/delDialog/DelDialog';
import useMountedRef from '@/utils/UseMountedRef.jsx'
import {
  EllipsisOutlined
} from '@ant-design/icons';
import ComPagination from '@/components/comPagination/ComPagination.jsx'
const { Option } = Select;
const { TextArea } = Input;
export default function ElasticIp () {
  const mountedRef = useMountedRef();
  const [form] = Form.useForm()
  const { t } = useTranslation()
  const navigate = useNavigate()
  const [total, setTotal] = useState(0)
  const [params, setParams] = useState({
    keywords: '',
    page: 1,
    limit: 20,
    orderby: 'id',
    sort: 'desc'
  })
  const [info, setInfo] = useState({})
  const [title, setTitle] = useState(t('cloud.bas_btn2') + t('cloud.security_group'))
  const [loading, setLoading] = useState(false)
  const [btnLoading, setBtnLoading] = useState(false)
  const [isModalVisible, setIsModalVisible] = useState(false)
  const [common, setCommon] = useState()
  // 退款modal
  const [isShowModal, setIsShowModal] = useState(false)
  // 停用页面信息
  const [refundData, setRefundData] = useState()
  const [optType, setOptType] = useState('add')
  const [itemModal, setItemModal] = useState(false)
  const [list, setList] = useState([])
  const [cloudList, setCloudList] = useState([])
  // 停用参数
  const [refundParams, setRefundParams] = useState({
    host_id: '',
    suspend_reason: [],
    type: ""
  })

  const columns = [
    {
      title: t('menu.float') + 'IP',
      dataIndex: 'ip',
      key: 'ip',
      ellipsis: true,
      width: 250
    },
    {
      title: t("elastic.bw"),
      dataIndex: 'bw_size',
      key: 'bw_size',
      ellipsis: true,
      width: 200,
      render: (_, record) => (
        <>
          {
            record.bw_size === 0 ? t('common.not') : record.bw_size + 'Mbps'
          }
        </>
      )
    },
    {
      title: t("menu.cloud"),
      dataIndex: 'cloud_name',
      key: 'cloud_name',
      ellipsis: true
    },
    {
      title: t("elastic.free"),
      dataIndex: 'first_payment_amount',
      key: 'first_payment_amount',
      ellipsis: true,
      width: 150,
      render: (_, record) => (
        common && <>
          {common.currency_prefix}{record.first_payment_amount}/{record.billing_cycle_name}
        </>
      )
    },
    {
      title: t("common.manage"),
      dataIndex: 'manage',
      key: 'manage',
      width: 100,
      render: (_, record) => (
        <>
          {
            record.status === 'Active' ? <div className="com-opt-pop" id={'com-opt' + record.id}>
              <Popover getPopupContainer={() => document.getElementById('com-opt' + record.id)}
                content={
                  <>
                    <p className="cloud-opt" onClick={() => editItem(record)}>{record.cloud_name ? t("common.unmount") : t("common.mount")}</p>
                    <p className="cloud-opt" onClick={() => deleteItem(record)}>{t("common.delete")}</p>
                  </>
                } placement="topRight">
                <p className="opt-icon"><EllipsisOutlined /></p>
              </Popover>
            </div> : null
          }
        </>
      )
    }
  ]
  useEffect(() => {
    getCommonConfig()
    getData()
    getCloud()
  }, [])
  // 接口请求
  // 弹性ip列表
  const getData = async () => {
    try {
      setLoading(true)
      const res = await elastic.getIpList(params)
      if (mountedRef.current) {
        setList(res.data.data.list)
        setTotal(res.data.data.count)
        setLoading(false)
      }
    } catch (error) {
      showDialog(error.data.msg, 'error')
      setLoading(false)
    }
  }
  // 通用配置
  const getCommonConfig = async () => {
    try {
      const res = await getCommon()
      if (mountedRef.current) {
        setCommon(res.data.data)
      }
    } catch (error) {
    }
  }
  // 实例列表
  const getCloud = async () => {
    try {
      const res = await elastic.getCloudList({
        page: 1,
        limit: 1000
      })
      const temp = res.data.data.list.filter(item => item.status === 'Active')
      if (mountedRef.current) {
        setCloudList(temp)
      }
    } catch (error) {
    }
  }

  // 事件操作
  // 删除按钮
  const deleteItem = (e) => {
    setRefundParams({ ...refundParams, host_id: e.id })
    setOptType('delete')
    deleteOpt(e.id)
  }
  const closeDialog = () => {
    setIsModalVisible(false)
  }
  // 执行删除操作，走退款流程
  const deleteOpt = async (host_id) => {
    try {
      // 先获取该产品是否停用
      const result = await cloud.refundMsg(host_id)
      if (result.data.data.refund && result.data.data.refund.status === 'Suspending') {
        return showDialog(t('elastic.tip5'), "warning")
      }
      const res = await cloud.refund({ host_id })
      if (res.data.status === 200) {
        let list = res.data.data
        setRefundData(list)
        if (list.allow_refund === 1) {  // 允许退款
          setIsShowModal(true)
        } else {
          showDialog(t('common.tip1'), "warning")
        }
      }
    } catch (error) {
      showDialog(error.data.msg, "error")
    }
  }

  // 停用原因选择框
  const getReason = (e) => {
    setRefundParams({ ...refundParams, suspend_reason: e })
  }
  const getSelfReason = (e) => {
    setRefundParams({ ...refundParams, suspend_reason: e.target.value })
  }
  // 停用时间 选择框
  const getType = (e) => {
    setRefundParams({ ...refundParams, type: e })
  }

  // 确认停用申请
  const doRefund = () => {
    let isPass = true
    if (refundParams.suspend_reason.length === 0) {
      showDialog((refundData.reason_custom === 1 ? t('input') : t('select')) + t('common.stop_reason'), "error")
      return
    }
    if (!refundParams.type) {
      showDialog(t('select') + t('common.stop_time'), "error")
      return
    }
    if (isPass) {
      cloud.postRefund(refundParams).then(res => {
        if (res.data.status === 200) {
          showDialog("申请停用成功，请耐心等待审核", "success")
          setIsShowModal(false)
        }
      })
    }

  }

  /*  挂载/卸载操作 */
  const editItem = (row) => {
    setInfo(row)
    if (!row.cloud_name) { // 挂载
      setItemModal(true)
      form.setFieldsValue({
        id: row.id,
        host_id: ''
      })
    } else { // 卸载
      setInfo(row)
      setTitle(t('common.unmount') + t('network.elastic_ip'))
      setOptType('unmounut')
      setIsModalVisible(true)
    }
  }
  // 执行卸载
  const submitDel = async () => {
    try {
      const res = await elastic.unmountCloud(info.id)
      if (res.data.status === 200) {
        showDialog(t('common.unmount') + t('common.success'), 'success')
        getData()
      }
    } catch (error) {
      showDialog(error.data.msg, 'error')
    }
  }
  const handleCancel = () => {
    setItemModal(false)
    form.resetFields()
  }
  // 提交挂载
  const onFinish = async (values) => {
    try {
      setBtnLoading(true)
      const { id, host_id } = values
      const params = {
        id: info.id,
        host_id
      }
      const res = await elastic.mountCloud(params)
      if (res.data.status === 200) {
        showDialog(t('common.mount') + t('common.success'), 'success')
        getData()
        handleCancel()
      }
      setBtnLoading(false)
    } catch (error) {
      setBtnLoading(false)
      showDialog(error.data.msg, 'error')
    }
  };
  /* 分页 */
  const changePage = (page, size) => {
    setParams(params => {
      params.limit = size
      params.page = page
      return params
    })
    getData()
  }
  // 新建
  const createIp = () => {
    navigate('/create_elastic_ip')
  }
  return (
    <div className='elastic-ip'>
      <div className="top">
        <span className="tip">{t('elastic.tip')}</span>
        <div className="create" onClick={createIp}>
          {t('common.buy') + t('network.elastic_ip')}
        </div>
      </div>
      <Table columns={columns} dataSource={list} loading={loading}
        rowKey={record => record.id} pagination={false} className='ip-tab' />
      <ComPagination total={total} page={params} changePage={changePage} ></ComPagination>

      {/* 挂载 */}
      <Modal title={t('common.mount') + t('network.elastic_ip')} visible={itemModal} onCancel={handleCancel}
        className='com-modal'
        closable={false}
        footer={false}>
        <Form name='basic'
          form={form}
          labelCol={{
            span: 3,
          }}
          labelAlign='left'
          onFinish={onFinish}
          autoComplete='off'
          colon={false}
        >
          <Form.Item
            label={t('menu.cloud')}
            name='host_id'
            rules={[{
              required: true,
              message: t('select') + t('menu.cloud'),
            },
            ]}
          >
            <Select>
              {
                cloudList.map(item => {
                  return (
                    <Option value={item.id} key={item.id}>{item.name}</Option>
                  )
                })
              }
            </Select>
          </Form.Item>
          <Form.Item className='f-btn'>
            <Button className='submit' htmlType="submit" loading={btnLoading}>{t('common.sure')}</Button>
            <Button className='cancel' onClick={handleCancel}>{t('common.cancel')}</Button>
          </Form.Item>
        </Form>
      </Modal>
      {/* 卸载 */}
      {
        isModalVisible && <DelDialog
          closeDialog={closeDialog} submitDel={submitDel}
          title={title}
          text={
            t('elastic.tip1') + t('common.unmount') + t('network.elastic_ip') + info.ip + '\n' + t('elastic.tip2')
          }
        ></DelDialog>
      }
      {/* 退款 */}
      {
        common && refundData ? <Modal
          className='refund-modal'
          visible={isShowModal}
          footer={null}
          centered={true}
          closable={false}
          style={{ borderRadius: "10px", overflow: "auto" }}
          maskClosable={true}
          keyboard={true}
          getContainer={false}
          onCancel={() => setIsShowModal(false)}
        >
          <div className="modal-unopen">
            <div className="modal-title">{t('common.apply_refund')}</div>
            <div className="modal-form">
              <div className="config-item">
                <div className="row-l">{t('common.product_info')}</div>
                <div className="row-r">
                  <div className="item">
                    <p className="l-tit">{t('common.product_config')}：</p>
                    <div className="r-text">
                    </div>
                  </div>
                  <div className="item">
                    <p className='l-tit'>{t('common.buy_time')}：</p>
                    <div className="r-text">
                      <p>{moment(refundData.host.create_time * 1000).format("YYYY-MM-DD HH:mm")}</p>
                    </div>
                  </div>
                  <div className="item">
                    <p className='l-tit'>{t('common.buy_money')}：</p>
                    <div className="r-text">
                      <p>{common.currency_prefix}{refundData.host.first_payment_amount}{common.currency_code}</p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="config-item">
                <div className="row-l">{t('common.stop_reason')}</div>
                <div className="row-r">
                  {
                    refundData.reason_custom !== 1 ?
                      <Select mode="multiple" showArrow allowClear size='large' style={{ width: "100%" }} onChange={getReason}>
                        {
                          refundData.reasons.map(item => {
                            return <Option key={item.id} value={item.id}>{item.content}</Option>
                          })
                        }
                      </Select>
                      :
                      <TextArea onChange={getSelfReason} rows={4} placeholder="请输入停用原因" />
                  }
                </div>
              </div>
              <div className="config-item">
                <div className="row-l row-center">
                  {t('common.stop_time')}
                </div>
                <div className="row-r">
                  <Select size='large' style={{ width: "100%" }} onChange={getType}>
                    <Option value="Expire">{t('common.expire')}</Option>
                    <Option value="Immediate">{t('common.immediate')}</Option>
                  </Select>
                </div>
              </div>
              <div className="config-item money">
                <div className="row-l">
                  {t('common.refund_amount')}
                </div>
                <div className="row-right">

                  {
                    refundParams.type === "Immediate" ?
                      // 立即停用
                      common.currency_prefix + refundData.host.amount + common.currency_code
                      :
                      common.currency_prefix + '0' + common.currency_code
                    // 到期停用
                  }
                  {/* {common.currency_prefix}{refundData.host.amount} */}
                </div>
              </div>
            </div>
            <div className="modal-footer">
              <Button className='sure' onClick={doRefund}>
                {t('common.sure_refund')}
              </Button>
              <Button className='cancel' onClick={() => setIsShowModal(false)}>{t('common.cancel')}</Button>
            </div>
          </div>
        </Modal>
          : null
      }
    </div >
  )
}
