// 弹性ip

import request from "../utils/request";

// 列表
export function getIpList (params) {
  return request.get(`/idcsmart_cloud_ip`, { params });
}
// 挂载
export function mountCloud (params) {
  return request.put(`/idcsmart_cloud_ip/${params.id}/mount`, params);
}
//卸载
export function unmountCloud (id) {
  return request.put(`/idcsmart_cloud_ip/${id}/umount`);
}
// 删除
export function deleteIp (id) {
  return request.delete(`/idcsmart_cloud_ip/${id}`);
}
// 获取套餐（带宽类型）
export function getPackage (params) {
  return request.get(`/product/${params.id}/idcsmart_cloud_ip/package`, { params });
}
/* 实例列表 */
export function getCloudList (params) {
  return request.get(`/idcsmart_cloud`, { params });
}
// 实例详情
export function getCloudDetail (id) {
  return request.get(`/idcsmart_cloud/${id}`);
}
// 根据配置获取价格
export function getIpPrice (params) {
  return request.get(`/idcsmart_cloud_ip/duration_price`, { params });
}

// 获取内页弹性ip列表
export function getCloudIp (params) {
  return request.get(`/idcsmart_cloud/${params.id}/ip`, { params });
}